import Logo from "../../img/bilaine.svg"
import Dragon from "../../img/dragon.png"
import  "./index.scss"
import {Link} from "react-router-dom";
import React, {useEffect, useState} from "react";
import ScrollToTop from "../../components/utils/ScrollToTop";

const MainPage = () => {
    const [firstText, setFirstText] = useState(true)
    useEffect(()=>{
        document.body.style.overflow='hidden'
    },[])
    ScrollToTop()
    window.addEventListener('resize', () => {
        // получаем текущее значение высоты
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
    });
    let maxHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    return (
        <div className="mainPageContainer" style={{maxHeight: maxHeight}}>
            <div className={'bg'}/>
            <div className="mainText">
             <img src={Logo}/>
                {firstText ?
                <div>
                <div style={{marginBottom: 12}}>Часто у нас есть стереотипные представления о работе. И сфера продаж — одна из тех, где стереотипов больше всего.</div>
                <div>Но не все компании одинаковые! Давай проверим, насколько твой взгляд на работу свободен от предубеждений? Доверься интуиции и выиграй призы!</div>
                </div> : <div>На экране будут появляться утверждения о работе в сфере b2b-продаж. Твоя задача — решить, бывает ли так в компаниях. Если считаешь, что такое бывает — свайпай утверждение вверх к «Так бывает!», а если думаешь, что такого нет — свайпай вниз к фразе «Так не бывает!</div>
                }
            </div>
            <div className="imgDragon">
                <img src={Dragon}/>
                {/*<div className={'img'}/>*/}
                {firstText ?
                    <button onClick={()=>setFirstText(false)}>
                        В игру!
                    </button>
                    :
                    <Link to={'/game/1'}>
                        <button>Вперед!</button>
                    </Link>
                }
            </div>
        </div>
    );
};

export default MainPage;
